html{
	height: 100%;
}
body{
	height:100%;
}
div#root{
	height: 100%;
}
.loginContainer{
	display: flex;
	height: 100%;
	width: 100%;
	justify-content:center;
	align-items: center;
	background-color: #84E0F4;
}
.loginBox{
	border-radius: 20px;
	background-color: white;
	height:60%;
	width:30%;
	margin: 20px;
	padding: 20px;
}
.selected{
	background-color:lightblue;
	padding: 10px;
	border:none;
	margin: 10px;
}
.notSelected{
	background-color:lightgray;
	border: none;
	padding: 10px;
	margin: 10px;
}
#lll{
	text-align: center;
}
.userButtonHolder{
	display:flex;
	justify-content: center;
}
.emInp{
	border: none;
	padding: 10px;
	margin:10px 0px 10px 25px ;
}
.psInp{
	border: none;
	padding: 10px;
	margin: 5px;
}
.logButton{
	display: flex;
	justify-content: center;
}
.logSubmitButton{
	border: none;
}
.usernameContainer{
	display: flex;
	flex-direction:column;
	height: 300px;
	overflow: auto;
}
.usernameButton{
	padding: 10px;
	margin: 10px;
	width: fit-content;
	display:flex;
	align-items: center;}
.addButton{
	postion: absolute;
	bottom: 40px;
	left: auto;
	right: auto;
}
.nobox{
	width: 100%;
	background-color:#84E0F4 ;
	padding: 10px;
	margin: 10px;
}
.nodivv{
	background-color: white;
	padding: 10px;
	margin: 10px;
}